.heading-h2 {
  font-size: clamp(2.6rem, 5vw, 4rem);
}

.p-line {
  font-size: clamp(1.8rem, 1vw, 2rem);
}

@media (width > 60em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

@media (width > 60em) {
  .col-3 {
    flex: 0 1 calc(33.3333333333% - 1.5rem);
  }
}

.manage {
  padding: 8rem 0;
}
.manage .d-flex-layout {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.manage__text {
  padding-bottom: 6rem;
}
@media (width > 60em) {
  .manage__text {
    padding-right: 8rem;
  }
}
.manage__video {
  position: relative;
}
.manage__video img {
  border-radius: 2rem;
}
.manage__video-play {
  position: absolute;
  font-size: 2rem;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
@media (width > 60em) {
  .manage__video-play {
    top: 50%;
    left: 0;
    transform: translate(-50%, -50%);
  }
}
.manage__video-play .fa-play {
  background: #ed4c5c;
  color: #fff;
  width: 8rem;
  height: 8rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5rem;
}
.manage .link {
  text-transform: capitalize;
  color: #ed4c5c;
  display: block;
  font-size: clamp(1.8rem, 1vw, 2rem);
}